import { Component, OnInit } from '@angular/core';
import { AssetAssetArchivedService, AssetStatusService, AssetAssetService, AssetPartIdService, AssetLifeLimitService, VendorVendorService, AssetModelxService, AssetBinLocationService, VendorManufactureService, AssetConditionMonitoringService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Asset_archived, Status, Modelx, Part_id, Vendor, Manufacture, Bin_location, Life_limit, Condition_monitoring } from 'src/app/sdk/asset/models';
import { FormControl } from '@angular/forms';
import { SearchAssetComponent } from 'src/app/components/search-item/search-asset/search-asset.component';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private assetAssetArchivedService: AssetAssetArchivedService,
        private assetStatusService: AssetStatusService,
        private assetAssetService: AssetAssetService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private drawerService: NzDrawerService,
        private assetPartIdService: AssetPartIdService,
        private assetLifeLimitService: AssetLifeLimitService,
        private vendorVendorService: VendorVendorService,
        private assetModelxService: AssetModelxService,
        private assetBinLocationService: AssetBinLocationService,
        private vendorManufactureService: VendorManufactureService,
        private assetConditionMonitoringService: AssetConditionMonitoringService,

    ) { }

    dataArchived: Asset_archived = {}
    id_asset_archived: string = ''

    listStatus: Status[] = []
    dataModel: Modelx = {}
    dataType: Part_id = {}
    dataVendor: Vendor = {}
    dataManufacture: Manufacture = {}
    dataBin: Bin_location = {}
    dataLifelimit: Life_limit = {};
    dataCondition: Condition_monitoring = {};


    ngOnInit() {
        this.id_asset_archived = this.activatedRoute.snapshot.paramMap.get('id_asset_archived')
        this.getPreeData()
        this.getData()
        this.form()

    }

    id_status_type: string = 'archived'
    getPreeData() {
        let param = {
            id_status_type: this.id_status_type
        }
        this.assetStatusService.getAssetStatuses(JSON.stringify(param)).subscribe(data => {
            this.listStatus = data.data
        })
    }

    getData() {
        this.assetAssetArchivedService.getAssetAssetArchivedsId(this.id_asset_archived).subscribe(
            data => {
                this.dataForm.setValue(data.data);
            }
        )
    }


    dataForm: FormGroupTypeSafe<Asset_archived>
    form() {
        this.dataForm = this.fb.group<Asset_archived>({
            id_status: new FormControl(''),
            tanggal_archived: new FormControl(''),
            archived_reason: new FormControl(''),
            Keterangan: new FormControl(''),
            id_asset: new FormControl(''),
            id_user: new FormControl(''),
            id_asset_archived: new FormControl(''),
            id_dokumen: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            id_data_flow: new FormControl(''),
            status: new FormControl(''),
            posisi: new FormControl(''),
            nomor: new FormControl(''),
        })
    }

    id_asset: string = ''
    selectAsset() {
        const drawerRef = this.drawerService.create<SearchAssetComponent, { value: string }, string>({
            nzTitle: 'Search Asset',
            nzContent: SearchAssetComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_asset = data;
                this.dataForm.patchValue({ id_asset: this.id_asset })
                this.beforeAsset()
            }
        });
    }



    dataItem: any = {};

    beforeAsset() {
        console.log("panggie ko")
        this.assetAssetService.getAssetAssetsId(this.id_asset).subscribe(
            data => {
                this.dataItem.sn = data.data.sn;
                this.dataItem.id_model = data.data.id_model;
                this.dataItem.purchase_date = data.data.purchase_date;
                this.dataItem.purchase_number = data.data.purchase_number;
                this.dataItem.price = data.data.price;
                this.dataItem.id_bin_location = data.data.id_bin_location;
                this.dataItem.id_vendor = data.data.id_vendor;
                this.getModel(this.dataItem.id_model)
                this.getBin(this.dataItem.id_bin_location)
                this.getVendor(this.dataItem.id_vendor)
            }
        )
    }

    getModel(id_model) {
        this.assetModelxService.getAssetModelxesId(id_model).subscribe(
            data => {
                this.dataModel = data.data
                this.getPartId(this.dataModel.id_part_id)
                this.getConditionMonitoring(this.dataModel.id_condition_monitoring)
                this.getLifeLimit(this.dataModel.id_life_limit)
                this.getManufacture(this.dataModel.id_manufacture)
            }
        )
    }
    getPartId(id_part_id) {
        this.assetPartIdService.getAssetPartIdsId(id_part_id).subscribe(
            data => {
                this.dataType = data.data
            }
        )
    }
    getLifeLimit(id_life_limit) {
        this.assetLifeLimitService.getAssetLifeLimitsId(id_life_limit).subscribe(
            data => {
                this.dataLifelimit = data.data
            }
        )
    }
    getConditionMonitoring(id_condition_monitoring) {
        this.assetConditionMonitoringService.getAssetConditionMonitoringsId(id_condition_monitoring).subscribe(
            data => {
                this.dataCondition = data.data
            }
        )
    }
    getManufacture(id_manufacture) {
        this.vendorManufactureService.getVendorManufacturesId(id_manufacture).subscribe(
            data => {
                this.dataManufacture = data.data
            }
        )
    }

    getBin(id_bin_location) {
        this.assetBinLocationService.getAssetBinLocationsId(id_bin_location).subscribe(
            data => {
                this.dataBin = data.data
            }
        )
    }

    getVendor(id_vendor) {
        this.vendorVendorService.getVendorVendorsId(id_vendor).subscribe(
            data => {
                this.dataVendor = data.data
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataArchived = this.dataForm.value
        this.assetAssetArchivedService.putAssetAssetArchivedsId({ id: this.id_asset_archived, body: this.dataArchived }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/asset/maintenance/archived/list'])
    }

}
